<div><p class="text-start title-style"><i class="mr-1 cursor" :class="groupedStatusStyle" @click="setGroupStatus()"> </i>Card Zone {{ index+1 }}</p><div class="d-flex roles-style" v-for="(pair,index) in data"><div class="mr-1 cursor" @click="setSingleStatus(pair,index)"><i class="bi" :class="setSingleCheckColor(pair.checked)"> </i></div><div class="d-flex justify-content-between flex-grow-1"><p class="pole-style w-50 text-start" v-for="person in pair?.roles"><i class="bi bi-person-badge icon-color" :style="SetTeamColor(person.color)"> </i>{{ person.label }}</p></div></div></div>